<template>
    <div>

        <!-- 活动条 -->
        <div class="activity-box" v-if="activityFlag">
            <span class="invitation"><img src="static/images_icon/invitation.png" alt=""></span>
            <o-button :text="'立即邀请'" class="invitation-btn" @btnClick="showInvitation"></o-button>
            <input type="button" readonly="readonly" class="activity-close" value="" @click="closeActivityBar" />
        </div>

        <common :loading="loading" :error="error" :datas="recommendOplace"></common>
        <div class="invite">
            <h1 class="invite-title">加入「 原点·Oplace 」</h1>
            <p class="invite-explain">下载免版权的照片、视频、音频，分享你自己<br>的作品给世界各地的人。</p>
            <o-button :text="'登录 / 注册'" :classStyle="'main-btn'" class="go-login" @btnClick="showLogin"></o-button>
        </div>
        <login v-if="flag" @closePopup="closePopup"></login>

        <!--邀请好友弹框-->
        <popup v-if="invitationFlag" class="invitationPop">
            <div slot="content" class="content">
                <div class="invitation-icon-box">
                    <img src="static/images_icon/invitation_icon.png" alt="" />
                </div>
                <h2 class="invitation-title">邀请好友加入「原点·Oplace」</h2>
                <p class="invitation-explain">每邀请成功<strong> 1 </strong>人，可获得原点5折卡1张（有效期：1天）</p>
                <ul class="share-box">
                    <li class="item" v-for="(item, index) in shareData" :key="index">
                        <a href="javascript:;" :class="item.classStyle">
                            <p class="share-name">{{ item.title }}</p>
                        </a>
                    </li>
                </ul>
            </div>
            <input type="button" name="" class="close-btn" slot="close" @click="invitationFlag=false" />
        </popup>
    </div>
</template>
<script>
    
    import OButton from '../component/button.vue';
    import Login from '../Popup/Login/login.vue';
    import Common from '../component/common.vue';
    import Popup from '../component/popup.vue';

    import OPLACE from '../../../oplace.js';
    
    export default {
        data() {
            return {
                recommendOplace: null,
                flag: false, // 登录框显示标志
                shareData: [
                    { classStyle: "weixin", title: '微信' },
                    { classStyle: "pyq", title: '朋友圈' },
                    { classStyle: "weibo", title: '微博' },
                    { classStyle: "qq", title: 'qq' }
                ],
                invitationFlag: false, // 邀请好友弹框显示标志
                activityFlag: true,
                loading: false, // 加载中
                error: null, // 加载错误
            }
        },
        created() {

            // 组件创建完后获取数据
            this.getRecommendOplace();
        },
        watch: {},
        methods: {

            // 获取推荐作品信息
            getRecommendOplace () {
                this.recommendOplace = OPLACE.recommend;
            },
            showLogin () {
                this.flag = !this.flag;
            },
            closePopup (value) {
                this.flag = value;
            },

            // 显示邀请弹窗
            showInvitation () {
                this.invitationFlag = !this.invitationFlag;
            },

            // 关闭活动邀请条
            closeActivityBar () {
                this.activityFlag = !this.activityFlag;
            }
        },
        components: {
            OButton,
            Login,
            Common,
            Popup,
        }
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped media="screen">

    /*活动条*/
    .activity-box {
        width: 100%;
        border-radius: 200px;
        background-color: #F4F4F4;
        height: 48px;
        margin-top: 10px;
        margin-bottom: 10px;
        display: flex;
        justify-content: center;
        position: relative; 
        .invitation {
            width: 370px;
            height: 100%;
            padding-top: 13px;
            float: left;
        }
        .invitation-btn {
            padding: 0 20px;
            font-size: 16px;
            color: #48ABFF;
            background: none;
            box-shadow: none;
            z-index: 0;
            &:active {
                color: #3D96E2;
                background: none;
            }
        }
        .activity-close {
            width: 48px;
            height: 48px;
            border-radius: 50%;
            background: url(../../../static/images_icon/sprite.png) no-repeat;
            background-position: -222px -105px;
            background-size: auto 300px;
            position: absolute;
            top: 0;
            right: 0;
            &:hover {
                background-position: -222px -67px;
            }
            &:active {
                background-color: rgba(255, 255, 255, 0);
            }
        }
    }

    .invite {
        width: 100%;
        height: 290px;
        background: #fafafa;
        padding-top: 52px;
        margin-top: 75px;
        text-align: center;
        font-size: 18px;
        color: #707070;
        .invite-title {
            color: #343434;
            font-size: 28px;
            font-weight: bold;
        }
        .invite-explain {
            margin: {
                top: 20px;
                bottom: 28px;
            }
        }
        .go-login {
            width: 158px;
            height: 40px;
            margin: 0 auto;
            font-size: 15px !important;
            font-weight: bold;
        }
    }

    .invitationPop {
        .content {
            width: 460px;
        }
    }

    .invitation-icon-box {
        width: 85px;
        height: auto;
        margin: 0 auto;
        margin-top: 50px;

    }
    .invitation-title {
        font-size: 20px;
        width: 100%;
        text-align: center;
        padding-top: 24px;
    }
    .invitation-explain {
        font-size: 12px;
        color: #626262;
        text-align: center;
        padding-top: 5px;
    }
    .share-box {
        width: 100%;
        display: flex;
        justify-content: center;
        overflow: hidden;
        padding-top: 32px;
        padding-bottom: 50px;
        text-align: center;
        .item {
            width: 42px;
            margin: 0 14px;
            .share-name {
                line-height: 8.6;
            }
            a {
                height: 42px;
                width: 100%;
                background: url(../../../static/images_icon/sprite_share.png) no-repeat;
                background-size: auto 120px;
            }
            a.weixin {
                background-position: 0 0;
            }
            a.pyq {
                background-position: 0 -78px;
            }
            a.weibo {
                background-position: -89px -78px;
            }
            a.qq {
                background-position: -89px 0;
            }
        }
    }
</style>



